@import './../variables/_mixins';

.nav {
  padding-top: 44px;
  display: flex;
    align-items: center;
    justify-content: space-between;
    @media all and (max-width: 980px) {
      padding-top: 40px;
      max-width: initial;
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
      width: 100vw;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      background: #FFFFFF;
      transform: translateX(-100%);
      transition: transform 0.5s ease;
      -webkit-transform: translateX(-100%);
      -moz-transform: translateX(-100%);
      -ms-transform: translateX(-100%);
      -o-transform: translateX(-100%);
      -webkit-transition: transform 0.5s ease;
      -moz-transition: transform 0.5s ease;
      -ms-transition: transform 0.5s ease;
      -o-transition: transform 0.5s ease;    
      z-index: 10; 
      top:42px;
    }
    &.active {
      transform: translateX(0);
      -webkit-transform: translateX(0);
      -moz-transform: translateX(0);
      -ms-transform: translateX(0);
      -o-transform: translateX(0);
      z-index: 10;
    }
  &__item {
    font-family: 'Roboto';
    @include text(15px, 500, 18px, #272674);
    @media all and (max-width: 980px) {
      font-size: 24px;
    }
  }
  &__item__wrap{
    display: flex;
    flex-direction: column;
  }
  &__wrap-tel {
    display: flex;
    align-items: center;
  }
  &__icon-tel {
    margin-right: 15px;
  }
  &__button {
    background: #262A66;
    width: 179px;
height: 40px;
@include text(16px, 700, 22px, #FFFFFF);
  }
  &__link {
    @include text(15px, 500, 18px, #272674);
    font-family: 'Roboto';
    @media all and (max-width: 980px)  {
      @include text(24px, 500, 18px, #272674);
    }
  }
  &__link:hover {
    opacity: 0.9;
  }
  &__list {
    display: flex;
    align-items: center;
    @media all and (max-width: 980px) {
      flex-direction: column;
      max-height: 500px;
    height: 100%;
    justify-content: space-between;
    padding: 30px;
    }
  }
  &__item:not(:last-child) {
    margin-right: 60px;
    @media all and (max-width: 1146px) {
      margin-right: 40px;
    }
    @media all and (max-width: 1060px) {
      margin-right: 20px;
    }
  }
  &__phone{
    @include text(16px, 900, 22px, #272674);
    text-decoration: none;
    @media all and (max-width: 1060px) {
      @include text(14px, 900, 20px, #272674);
    }
  }
  &__span {
    @include text(12px, 400, 16px, #121212);
    margin-top: 5px;
  }
  &__mobile__logo {
    margin-left: 20px;
    margin-top: 12px;
  }
  &__mobile{
    display: none;
    position: fixed;
    z-index: 11;
    
    @media all and (max-width: 980px)  {
      display: flex;
      
      align-items: center;
    margin-top: 20px;
    // z-index: 2;
    background-color: #FFFFFF;
    background-size: 100%;
    width: 100%;
    right: 0px;
    top: -20px;
    height: 77px;
    }
  }
}
