.logo {
  @media all and (max-width: 980px) {
    display: none;
  }
}
.burger {
  display: none;
  width: 40px;
  height: 40px;
  position: fixed;
  right: 20px;
  top: 20px;
  // background: url(./../images/burger.svg) no-repeat center;
  cursor: pointer;
  transition: 0.2s;
  background-size: cover;
  z-index: 101;
  @media all and (max-width: 980px)  {
    display: block;
  }
}